@font-face {
    font-family: 'KOTRAHOPE';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2110@1.0/KOTRAHOPE.woff2') format('woff2');
}

* {
    margin: 0;
    padding: 0;
    font-family: 'KOTRAHOPE';
    text-decoration-line: none;
}

body {
    background-color: #fff;
}

ol, ul {
	list-style: none;
}

.off td {
    background: #eee;
    color: #ccc;
}

#error {
    display: flex;
    justify-content: center;
    font-weight: 400;
    font-size: 24px;
}